.neonButton {
    position: relative;
    background-color: transparent;
    font-size: 32px;
    cursor: pointer;
    color: var( --main-color);
    border: var( --main-color) 4px solid;
    border-radius: 20px;
    padding: 10px 16px;
    text-shadow: 0 0 16px var( --main-color), 0 0 32px var( --main-color);
    box-shadow: 0 0 16px var( --main-color), inset 0 0 16px var( --main-color);
}

.neonButton::before {
    content: "";
    pointer-events: none;
    position: absolute;
    background-color: var(--main-color);
    top: 50px;
    right: 0;
    width: 100%;
    height: 100%;
    transform: perspective(11px) rotateX(40deg)scale(1, 0.35);
    filter: blur(90px);
    opacity: 0.7;
}

.neonButton::after {
    content: "";
    position: absolute;
    border-radius: 10px;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 20px var( --main-color), 0 0 40px var( --main-color);
    z-index: -1;
    opacity: 0;
    background-color: var(--main-color);
    transition: opacity 200ms linear;
}

.neonButton:hover, .neonButton:focus {
    color: black;
    text-shadow: none;
}

.neonButton:hover::before, .neonButton:focus::before {
    opacity: 1;
}

.neonButton:hover::after, .neonButton:focus::after {
    opacity: 1;
}